html {
  background: rgb(234, 234, 234);
}

* {
  font-family: inter;
  box-sizing: border-box;
}

body {
  margin: 0px;
}

.appContainer {
  z-index: 2;
  width: 550px;
  height: 852px;
  border-radius: 8.95px;
  background: white;
  margin: 100px auto;
}

.NavbarContainer {
  height: 70px;
  box-shadow: 0px 3px 8px -3px rgba(0, 0, 0, 0.2);
}
.NavbarContainer img {
  margin: 20px 30px;
}

.heroContainer {
  display: flex;
  flex-direction: column;
  margin: 26px 40px;
}
.heroContainer .hero-grid-Container {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  height: 250px;
  margin: 0px 30px;
  justify-self: center;
}
.heroContainer .hero-grid-Container div {
  display: flex;
  flex-direction: column;
  margin: 3px;
}
.heroContainer .hero-grid-Container div > .hero-image {
  margin-bottom: 10px;
}
.heroContainer .hero-grid-Container div:nth-child(1) > img:first-child {
  margin-top: 45px;
}
.heroContainer .hero-grid-Container div:nth-child(2) > img:first-child {
  margin-top: 10px;
}
.heroContainer .hero-grid-Container div:nth-child(3) > img:first-child {
  margin-top: 30px;
}
.heroContainer .hero-grid-Container div:nth-child(5) > img:first-child {
  margin-top: 20px;
}
.heroContainer .title-text {
  margin: 22px 0px 16px 0px;
}
.heroContainer .title-subtext {
  width: 300px;
  margin: 0px;
}

.cardsArray {
  display: flex;
  overflow-x: scroll;
}
.cardsArray > * {
  margin-right: 18px;
}
.cardsArray > :first-child {
  margin-left: 37px;
}
.cardsArray::-webkit-scrollbar {
  display: none;
}

.cardContainer {
  min-width: 176px;
  position: relative;
}
.cardContainer img {
  width: 100%;
  height: 235px;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 10px;
}
.cardContainer button {
  position: absolute;
  top: 0px;
  left: 0px;
  margin: 5px;
  font-size: 10px;
  padding: 3px;
  border: none;
  background: white;
  border-radius: 3px;
}
.cardContainer .cardMainImage, .cardContainer .cardInfo, .cardContainer .title, .cardContainer .cost {
  margin-bottom: 7px;
}
.cardContainer .cardInfo {
  display: flex;
}
.cardContainer .cardInfo .cardStarImage {
  width: 14px;
  height: 14px;
}
.cardContainer .cardInfo .cardStarRating, .cardContainer .cardInfo .cardRatingCount, .cardContainer .cardInfo .cardCountry {
  font-size: 12px;
  margin-left: 4px;
}
.cardContainer .cardInfo .cardRatingCount, .cardContainer .cardInfo .cardCountry {
  color: rgb(172, 172, 172);
}
.cardContainer .title, .cardContainer .cost {
  font-size: 12px;
}

.colorString {
  background: rgb(238, 238, 238);
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 10px;
  padding: 10px;
  margin: 10px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.4);
}/*# sourceMappingURL=index.css.map */